import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

function loadView(view) {
  return () => import(/* webpackChunkName: "view-[request]" */ `@/components/${view}`)
}

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: '首页',
      component: loadView('index')
    },
    {
      path: '/login',
      name: '登录',
      component: loadView('login')
    },
    {
      path: '/register',
      name: ' 注册',
      component: loadView('register')
    },
    {
      path: '/member',
      name: '个人主页',
      component: loadView('member')
    },
    {
      path: '/shop',
      name: ' 礼品商城',
      component: loadView('shop')
    },
    {
      path: '/buy',
      name: '立即采购',
      component: loadView('buy')
    },
    {
      path: '/order',
      name: '订单管理',
      component: loadView('order')
    },
    {
      path: '/check',
      name: '查件补发',
      component: loadView('check')
    },
    {
      path: '/capitalDetail',
      name: '资金明细',
      component: loadView('capitalDetail')
    },
    {
      path: '/recharge',
      name: '充值',
      component: loadView('recharge')
    },
    {
      path: '/cost',
      name: '物流成本',
      component: loadView('cost')
    },
    {
      path: '/about',
      name: '关于我们',
      component: loadView('about')
    },
    {
      path: '/article',
      name: '文章',
      component: loadView('article'),
      children: [
        {
          path: '/',
          component: loadView('articleTitle'),
        },
        {
          path: '/article/content',
          component: loadView('articleContent'),
        }]
    }
  ]
})



